@import "../../../scss/mixins/color";

.date_dropdown {
    display: flex;
    justify-content: space-between;
    margin-top: 22px;

    &__header {
        font-family: Montserrat-bold;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 15px;
        text-transform: uppercase;
        @include color('d', 1);
        margin-bottom: 5px;
    }

    &__form {
        position: relative;
        display: flex;
        width: 150px;
        border: 1px solid rgba(31, 32, 65, 0.25);
        min-height: 44px;
        box-sizing: border-box;
        border-radius: 4px;

        &:hover {
          border: 1px solid rgba(31, 32, 65, 0.5);
        }
    }

    &__input {
        border: none;
        outline: none;
        width: 91px;
        vertical-align: middle;
        margin: 12px 0px 11px 15px;

        &::placeholder {
          @include color('d', 0.75);
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 24px;
        }
    }

    &__btn {
        width: 42px;
        height: 42px;
        border: none;
        border-radius: 4px;
        background-color: transparent;
        padding: 0;
    }

    &__svg {
        width: 42px;
        height: 42px;
        fill: #1F2041;
        fill-opacity: 0.5;
    }

